import React, { Suspense, useRef, useState } from "react"

// import react-three-fiber
import { Canvas, extend, useThree } from "react-three-fiber"

//import Stats from 'three/addons/libs/stats.module.js';
import { OrbitControls } from 'three/addons/controls/OrbitControls.js';
extend({OrbitControls});

import Architect from "../components/architect";
import "./canvas.css";

import { Box } from "../common/three/box";
import { TextMesh, TexturedPlane } from "../common/three/text";
import { Scene } from "../common/three/scene";
import { subscribe } from "../common/events";

const CameraControls = (props: any) => {
  const initialArgs: any[] = [];
  const [controlArgs, setControlArgs] = useState(initialArgs);

  useThree(function({camera, gl}) {
    if(!controlArgs.length) {
      console.log('Setting Camera control args: ', camera, gl.domElement);
      setControlArgs([camera, gl.domElement]);
    }
    // camera.rotation.set(deg2rad(30), 0, 0);
  });

  return (<>{
    controlArgs.length == 2 &&
    //@ts-ignore
    <orbitControls args={controlArgs} target={[0, 0, 0]}/>}
    </>
  );
};

const CanvasPage = () => {
  const [canvasReady, setCanvasReady] = useState(false);
  const canvas: any = useRef();

  const canvasOnCreated = ({gl}: any) => {
    console.log('canvasOnCreated', canvas, canvas.current.camera, gl)
    if( 2%2 ) {
      gl.setClearColor("#335")
    }
    setCanvasReady(true);
  };

  //subscribe('onMeshCreated', onMeshCreated)

  const userInfo = {
    name: 'M Sheriff',
  };

  return <Architect pageTitle={`Canvas`} openSidebar={false}>
    <div className={`three-canvas-wrapper`}>
    <Suspense fallback={`Render canvas...`}>
      <Canvas camera={{ position: [0, 2, 10] }} ref={canvas} onCreated={canvasOnCreated}>
        {canvasReady && <>
          <CameraControls />
          <TexturedPlane size={100} elevation={-5} />

          <ambientLight intensity={0.2} />
          <pointLight position={[10, 50, 10]} />

          <Box position={[0, 0, 0]} />
          <Box position={[1.2, 0, 0]} />
          <Box position={[-1.2, 0, 0]} autoRotate={true} />

          <TextMesh text={'Welcome'} size={1} position={[0, 3, -2]} color='gold' />
          <TextMesh text={userInfo.name} size={2} position={[0, 1.2, -2]} texture={'liquidGold'} autoRotate={true} />
        </>}
      </Canvas>
    </Suspense>
    </div>
  </Architect>
}

export default CanvasPage;
